<template>
    <div id="todos" class="panel panel-success">
        <div class="panel-heading" >
    <h class="panel-title"> My to do list</h>
        </div>
    <div class="panel-body">
        <td-head :todos="todos"></td-head>
        <ul class="list-group">
            <li class="list-group-item"
                v-bind:class="{'completed' : todo.completed}"
                v-for="(todo,index) in todos">
                <router-link :to="{name:'todo-info',params:{id: todo.id}}">{{todo.title}}</router-link>

                <button class="btn btn-warning btn-xs pull-right"
                        v-on:click="deleteTodo(index,todo)"
                >Delete
                </button>
                <button class="btn  btn-xs pull-right"
                        v-bind:class="[todo.completed ? 'btn-danger' : 'btn-success']"
                        v-on:click="toggleCompletion(todo)"
                >{{todo.completed? 'Done' : 'Do'}}
                </button>


            </li>
        </ul>
        <td-form :todos="todos"></td-form>
    </div>

    </div>
</template>

<script>
    import TdHead from './TdHead'
    import TdForm from './TdForm'
    export default {
        name:'todos',
        props:['todos'],
        methods:{
            deleteTodo(index,todo){
        this.axios.delete('http://test.loc/api/todo/'+todo.id).then
        (
                response=>{
            this.todos.splice(index,1)
        })

            },
            toggleCompletion(todo){
        this.axios.patch('http://test.loc/api/todo/'+todo.id+'/completed').then
        (
                response=>{
            todo.completed = !todo.completed
        })

            }
        },
    components: {
        TdForm,TdHead
    }

    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .completed{
        text-shadow:0.5px 0.5px #696969;
    }
</style>


