<template>
    <div class="post">
        <div class="loading" v-if="loading">
            Loading...
        </div>

        <div v-if="todo" class="content">
            <h2>{{ todo.title }}</h2>
            <p>{{ todo.body }}</p>
        </div>
        <router-link class="btn btn-success" :to="{name:'todos'}">返回</router-link>
    </div>
</template>

<script>
    export default {
        data () {
        return {
            loading: false,
            todo: null

        }
    },
    created () {
        // 组件创建完后获取数据，
        // 此时 data 已经被 observed 了
        this.fetchData()
    },
    watch: {
        // 如果路由有变化，会再次执行该方法
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            this.loading = true
            // replace getPost with your data fetching util / API wrapper
            this.axios.get('http://test.loc/api/todo/' + this.$route.params.id).then( response => {

                this.loading = false

                this.todo = response.data

            })
        }
    }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


