<template>

    <h1 class="alert alert-danger" v-if="todoCount">My todos {{todoCount}}</h1>
    <h1 class="alert alert-success" v-else>All Done</h1>

</template>

<script>
    export default {
        props:['todos'],
        computed:{
            todoCount:function(){
                return this.todos.filter(function(todo){
                    return !todo.completed;
                }).length;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


